@import "../../css/shared.scss";

.p-records{
  padding: 0 px(35) px(120) px(35);

  .list-item{
    display: flex;
    align-items: center;
    border-bottom: px(3) solid #5a089c;
    border-image: $linear-gradient 1 10;
    margin-top: px(20);

    div {
      &:nth-of-type(1){
        flex-grow: 1;

        p{
          &:nth-of-type(1){
            font-size: px(45);
          }
          &:nth-of-type(2){
            font-size: px(35);
            color: #b2b2b2;
          }
        }
      }

      &:nth-of-type(2){
        font-size: px(50);
        color: #ffd75e;
        font-weight: bold;
      }
    }
  }
}
